import useSWR, { SWRResponse } from 'swr';

function timeout(ms: number) {
  return new Promise((resolve) => setTimeout(resolve, ms));
}

interface Song {
  id: string;
  artist: string;
  title: string;
  youtubeId: string;
}

interface SuspenseResponseInterface extends SWRResponse<Song, any> {
  data: Song;
}

export const useSongDetails = (songId: string) => {
  return useSWR(
    `songs/${songId}`,
    async () => {
      await timeout(5000);
      return {
        id: songId,
        artist: '林二汶',
        title: '最後的信仰',
        youtubeId: 'tsL-kPHb0UU',
      };
    },
    { suspense: true }
  ) as SuspenseResponseInterface;
};
