import React, { Suspense } from 'react';
import styled from '@emotion/styled';
import { useParams } from 'react-router-dom';

import {
  PageContainer,
  SongContainer,
  SongDetailsContainer,
  LyricsContainer,
  UpcomingContainer,
  UpcomingSong,
} from './SongDetailsSkeleton';
import { YouTubePlayerSkeleton } from './YouTubePlayerSkeleton';
import { LyricsSkeleton } from '../lyrics/LyricsSkeleton';
import { useSongDetails } from './useSongDetails';

const YouTubePlayer = React.lazy(() => import('./YouTubePlayer'));
const LyricsDisplay = React.lazy(() => import('../lyrics/LyricsDisplay'));

interface RouteParams {
  songId: string;
}

export function SongDetailsPage() {
  const { songId } = useParams<RouteParams>();
  const { data } = useSongDetails(songId);

  return (
    <PageContainer>
      <SongContainer>
        <Suspense fallback={<YouTubePlayerSkeleton />}>
          <YouTubePlayer embedId={data.youtubeId} />
        </Suspense>

        <SongDetailsContainer>
          <SongTitle>
            {data.artist} - {data.title}
          </SongTitle>
          <LyricsContainer>
            <Suspense fallback={<LyricsSkeleton />}>
              <LyricsDisplay />
            </Suspense>
          </LyricsContainer>
        </SongDetailsContainer>
      </SongContainer>
      <UpcomingContainer>
        <UpcomingSong />
        <UpcomingSong />
        <UpcomingSong />
        <UpcomingSong />
        <UpcomingSong />
      </UpcomingContainer>
    </PageContainer>
  );
}

export default SongDetailsPage;

const SongTitle = styled.div`
  height: 26px;
  line-height: 26px;
`;
